.item {
  display: flex;
  flex-direction: column;
  max-width: 90%;
  margin: auto;
}
.span{
  width: 120px;
  border-bottom: 2px solid #8b92ab;
  margin: 0 auto;
  margin-top: 10px;
  margin-bottom: 40px;
}
@media screen and (min-width: 780px) {
  .item {
    max-width: 700px;
  }
}
