/* Gotham Pro 300 - latin */
@font-face {
  font-family: 'Gotham Pro';
  src: url('assets/fonts/Gotham Pro Light/GothamPro-Light.eot');
  src: url('assets/fonts/Gotham Pro Light/GothamPro-Light.eot?#iefix') format('embedded-opentype'),
  url('assets/fonts/Gotham Pro Light/GothamPro-Light.woff') format('woff'),
  url('assets/fonts/Gotham Pro Light/GothamPro-Light.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
}
