/* playfair-display-900 - latin */
@font-face {
  font-family: 'Playfair Display';
  font-style: normal;
  font-weight: 900;
  src: url('assets/fonts/Playfair Display Black/playfair-display-v14-latin-900.eot'); /* IE9 Compat Modes */
  src: local('Playfair Display Black'), local('PlayfairDisplay-Black'),
  url('assets/fonts/Playfair Display Black/playfair-display-v14-latin-900.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('assets/fonts/Playfair Display Black/playfair-display-v14-latin-900.woff2') format('woff2'), /* Super Modern Browsers */
  url('assets/fonts/Playfair Display Black/playfair-display-v14-latin-900.woff') format('woff'), /* Modern Browsers */
  url('assets/fonts/Playfair Display Black/playfair-display-v14-latin-900.ttf') format('truetype'), /* Safari, Android, iOS */
  url('assets/fonts/Playfair Display Black/playfair-display-v14-latin-900.svg#PlayfairDisplay') format('svg'); /* Legacy iOS */
}
